@import url("https://fonts.googleapis.com/css2?family=Montserrat:ital,wght@0,300;0,400;0,500;0,600;1,400;1,500&display=swap");
/**
*
* Testing
*
**/
.navbar {
  -webkit-box-flex: 0.2;
      -ms-flex: 0.2;
          flex: 0.2;
  z-index: 1;
  background-color: #323232;
}

.navbar-links {
  float: right;
  list-style: none;
  font-size: 1.4em;
  margin-right: 70px;
  text-align: right;
  padding: 0;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
  margin-top: 230px;
  position: fixed;
  right: 0;
  z-index: 1;
}

@media (max-width: 1024px) {
  .navbar-links {
    font-size: 1.2em;
    margin-right: 40px;
  }
}

.nav-link {
  text-decoration: none;
  color: #ffb7c5;
}

.nav-item {
  margin-top: 20px;
}

.nav-link:hover {
  color: #f30067;
}

.navbar-nav {
  width: 100%;
  height: 100%;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
  -webkit-box-pack: right;
      -ms-flex-pack: right;
          justify-content: right;
  position: relative;
  z-index: 1;
}

.nav-line {
  background-color: transparent;
  position: absolute;
  top: 0;
  right: 0;
  margin-right: 50px;
}

@media (max-width: 1024px) {
  .nav-line {
    margin-right: 10px;
  }
}

.line-main {
  stroke-width: 3px;
  position: fixed;
}

.button-neon {
  cursor: pointer;
  color: #f30067;
  border: #f30067 2px solid;
  padding: 0.35em;
  border-radius: 12px;
  text-shadow: 0 0 0.45em #f30067;
  -webkit-box-shadow: inset 0 0 0.3em 0 #f30067, 0 0 0.3em 0 #f30067;
          box-shadow: inset 0 0 0.3em 0 #f30067, 0 0 0.3em 0 #f30067;
  font-weight: 500;
}

.button {
  margin-top: 40px;
}

.button-neon:hover,
.button-neon:focus {
  background: #f30067;
  color: #323232;
  text-shadow: none;
  -webkit-transition: all 0.45s;
  transition: all 0.45s;
}
